<template>
    <div>
        <v-container fluid grid-list-md>
            <v-flex xs12 sm12 md12>
                <v-text-field
                    label=""
                    placeholder=""
                    box
                    v-model="title"
                    color="info"
                    type="text"
                    counter="30"
                ></v-text-field>
            </v-flex>

            <v-textarea
            name="input-7-1"
            box
            v-model="content"
            dark
            label=""
            color="info"
            auto-grow
            hint=" "
            ></v-textarea>
            <div>
                <v-btn color="info" class="float_right" @click="add_modify">/</v-btn>
            </div>
        </v-container>
    </div>
</template>

<script>
  export default {
    data: () => ({
        title: '',
        content: '',
        notice_num: 0,
    }),
    computed: {
      alba2_login() {
          return this.$store.getters.alba2_login
      },
      noice_no() {
          return this.$route.params.notice_no;
      }
    },
    mounted() {
        this.$store.commit('add_product_btn', false);
        this.$store.commit('add_notice_btn', false);
        this.notice_num = parseInt(this.noice_no, 10)
        if (this.notice_num > 0) {
            this.$store.dispatch('s_n_one', {
                n_no: this.notice_num
            }).then((res) => {
                console.log(res)
                this.title = res.title;
                this.content = res.content;
            })
        }
    },
    methods: {
        add_modify() {
            console.log(this.title.length)
            if (this.title == '') {
                alert(' ');
            }
            else if (this.content == '') {
                alert(' ');
            }
            else if (this.title.length > 30) {
                alert('  . 30  ')
            }
            else if (this.content.length > 300) {
                alert('  . 300  ')
            }
            else {
                let params = {
                    s_no: this.alba2_login.no,
                    title: this.title,
                    content: this.content,
                    n_no: this.notice_num
                }

                if (this.notice_num > 0) {
                    this.$store.dispatch('s_notice_update', params).then((res) => {
                        console.log(res);
                        if (res.result == 'SUCCESS') {
                            alert(' .');
                            // .

                            this.$router.push('/noticeList')
                        }
                        else {
                            alert('  .   ')
                        }
                    })

                }
                else {
                    this.$store.dispatch('s_notice_add', params).then((res) => {
                        console.log(res);
                        if (res.result == 'SUCCESS') {
                            alert('  .');
                            // .

                            this.$router.push('/noticeList')
                        }
                        else {
                            alert('  .   ')
                        }
                    })
                }
            }
        },
    }
  }
</script>
